.container {
  width: 100vw;
  height: 70vh;
  background-color: var(--quaternary-color);
}

.table {
  width: 65%;
  margin: auto;
}

.table td {
  border-radius: 30px;
  height: 50px;
  width: 500px;
}

.table th {
  width: 60px;
}

@media only screen and (min-width: 768px) {
  table {
    width: 70%;
  }
}

@media only screen and (min-width: 1200px) {
  table {
    width: 55%;
  }
}
